import React from "react";

import { navigate } from "gatsby"; //import navigate from gatsby

import styled from "styled-components";

import getSiteMetadata from "hooks/getSiteMetadata";

import Layout from "components/Layout";

import { navigation as NavigationList } from "constant/navigation";

const IndexWrapper = styled.div`
  display: flex;
  position: relative;
  width: 100%;
  padding: 3.5rem 2rem;
  flex-direction: column;
  align-items: center;
  text-align: center;
`;

const Header = styled.div`
  border: 2px solid gray;
  border-left: none;
  border-right: none;
  margin-top: 4rem;
  width: 100%;
  padding: 3rem 2rem;
`;

const StyledH1 = styled.h1`
  font-size: 2.25rem;
  line-height: 1.3;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 0;
  font-family: "Source Sans Pro", sans-serif;
`;

const StyledP = styled.p`
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-size: 0.8rem;
  line-height: 2;
  font-family: "Source Sans Pro", sans-serif;
  color: gray;
`;

const StyledButton = styled.button`
  display: block;
  line-height: 2.75rem;
  padding: 0 1.25rem 0 1.45rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-size: 0.8rem;
  border: none;
  box-shadow: none;
  background-color: transparent;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  outline: 0;
`;

const StyledNav = styled.nav`
  margin-top: 3.5rem;
`;

const StyledUl = styled.ul`
  display: flex;
  margin-bottom: 0;
  list-style: none;
  padding-left: 0;
  border: solid 1px gray;
  border-radius: 4px;
  li {
    padding-left: 0;
    border-left: solid 1px gray;
  }
  li:first-child {
    border-left: 0;
  }
`;

const Index = ({ location }) => {
  const { title, description } = getSiteMetadata();
  return (
    <Layout path={location && location.pathname}>
      <IndexWrapper>
        <Header>
          <StyledH1>{title}</StyledH1>
          <StyledP>{description}</StyledP>
        </Header>
        <StyledNav>
          <StyledUl>
            {NavigationList.map((nav, index) => (
              <li key={index}>
                <StyledButton
                  onClick={() => {
                    navigate(nav.link);
                  }}
                >
                  {nav.title}
                </StyledButton>
              </li>
            ))}
          </StyledUl>
        </StyledNav>
      </IndexWrapper>
    </Layout>
  );
};

export default Index;
